import { useState } from 'react';
import CustomLink from '../components/page/link';
import Page from '../components/page/page';
import Subtitle from '../components/page/subtitle';
import React from 'react';

import Title from '../components/page/title';
import { RouteString } from '../menuStructure';

export default function Motivation() {

  

  const [shortOpen, setShortOpen] = useState(false)
  const [longOpen, setLongOpen] = useState(false)


  return <Page>

    <Title>
      Motivation
    </Title>


    <br />

    <Subtitle>

      Kurz gefasst
    </Subtitle>
    <br />

    Jahre meines Lebens waren von extremen emotionalen Zuständen geprägt.

    Mit professioneller Hilfe, Meditation und
    Selbstbeobachtung habe
    ich meine eigenen Werkzeuge
    und Kniffe entwickelt,
    meine Lebensqualität wieder auf ein "normales" Maß zu bringen.
    <br />
    <br />
    Und darüber hinaus, zu lernen, was glücklich macht.



    <br />
    <br />

    Seit die Früchte dieser Arbeit
    klar ersichtlich sind,
    haben mich immer mehr Freund:innen und Bekannte
    auf meine ruhige und verständnisvolle, aber trotzdem
    analytische Art
    angesprochen, auf sie einzugehen und
    mich ihren Themen zu nähern.
    <br />

    Schließlich kam dadurch die Idee,
    meine Erfahrungen in persönlichen Sitzungen
    mehr Menschen zur Verfügung zu stellen.

    Mich motiviert die Wirkung meiner Arbeit:
    <br />
    <br />
    Glückliche Gesichter, die gelernt haben,
    wie sie sich selbst beobachten, akzeptieren und
    weiterentwickeln können.

    <br />
    <br />
    <CustomLink route={RouteString.Preise} pre=" ">Alles klar, wie viel kostet mich der Spaß?</CustomLink>

    <br />
    <br />

    <Subtitle>

      Länger gefasst
    </Subtitle>
    <br />



    Ein Mensch heilt hauptsächlich durch Reden und gehört werden.
    Er heilt durch die Beobachtung seines Verhaltens und seiner Gedanken.
    Er heilt durch das Verständnis, was er über sich selbst erlangt
    und die Akzeptanz, die dadurch in ihm zu gedeihen beginnt.
    <br />
    <br />

    In den Jahren der Beschäftigung mit mir selbst habe ich viele
    persönliche Strategien entwickelt,
    die genau diese Selbstbeobachtung schärfen und stärken.

    <br />
    <br />

    Mit der Zeit sehe ich immer klarer,
    wie viel selbstschadendes Verhalten
    uns unbewusst von unserer Umgebung antrainiert wird.
    <br />
    <br />
    Und dann leben wir es einfach weiter aus...

    <br />
    <br />

    Ohne bewusste Arbeit und eine:n Ansprechpartner:in,
    der oder die den Prozess begleitet und Verhalten spiegeln kann,
    verirren wir uns in unseren schädlichen Gewohnheiten.

    <br />
    <br />

    Dabei fehlt manchen Menschen vielleicht nur ein emotionale:r Unterstützer:in,
    die oder der selbst schmerzhafte Phasen durchlebt hat,
    und die Erfahrungen und Werkzeuge mitbringt,
    die ihr oder ihm dabei geholfen haben,
    diese Phasen zu überwinden.

    <br />
    <br />


    Zusätzlich wird in der heutigen Zeit das wirkliche und aufrichtige Zuhören
    und verstanden werden immer seltener.

    <br />
    <br />

    Manche Menschen können gar nicht mehr aufrichtig zuhören,
    weil sie währenddessen kontinuierlich über sich selbst nachdenken.

    <br />
    <br />

    Als ich diese Fähigkeit langsam wieder erlente,
    hat mich schockiert,
    für wie normal und selbstverständlich ich diesen Zustand
    lange Jahre meines Lebens gehalten habe.

    <br />
    <br />

    Für mich gibt es nichts Schöneres
    als meine Erfahrungen und Realisationen
    an meine Mitmenschen weiterzugeben.




    <br />
    <br />

    Meine Philosophie in der professionellen Begegnung mit Ihnen als Klient:in besteht
    darin, Sie zunächst mit dieser Fähigkeit zu beschenken.

    <br />

    Und bei Interesse Wege zu finden,
    wie Sie die fundamentalen Erkenntnisse, die daraus resultieren,
    in ihrem Handlungsreportoire weiter verfeinern und somit auch in Ihr Umfeld einfließen lassen können.

    <br />
    <br />

    Meine jahrelange Erfahrung in Selbstbeobachtung hilft mir dabei,
    die Kanten, Ecken und Fallstricke zu erkennen,
    die uns daran hindern,
    neue Perspektiven einzunehmen
    und ungesunde Gewohnheiten abzulegen.

    Durch sanftes Nachfragen ergibt sich schon bald ein
    Bild, an dem garbeitet werden kann.

    <br />
    <br />
    Wer den Prozess verinnerlicht hat,
    schaut sich nur noch beim Wachsen zu.
    <br />
    <br />

    Dabei ist jede Begegnung einzigartig und individuell.
    Ich folge also keinem Schema,
    sondern wir schauen gemeinsam,
    an welchem Punkt Sie sich befinden
    und wohin wir uns dann bewegen.
    <br />
    <br />

    Ich liebe den Menschen in seiner Vielfalt und Diversität
    und freue mich, wenn ich zu Wohlbefinden und Freude beitragen kann.
    <br />
    <br />



    {/*     Falls Sie sich detailliertere Informationen wünschen, lesen Sie gern mein Buch. 

    <br />
    <br /> */}

    <CustomLink route={RouteString.Angebote} pre=" ">Was bieten ich Ihnen genau an?</CustomLink>
    <br />
    <CustomLink route={RouteString.MeineGeschichte} pre=" ">Oder interessiert sie meine Geschichte?</CustomLink>
    <br />
    <CustomLink route={RouteString.Preise} pre=" ">Wie teuer ist eine Sitzung?</CustomLink>



  </Page>


}