import React from 'react';
import { Nav } from 'react-bootstrap';
import { Link, Route } from 'react-router-dom';
import CustomLink from '../components/page/link';
import Page from '../components/page/page';

import Title from '../components/page/title';
import { RouteString } from '../menuStructure';

import picture1 from "./../images/picture1.jpg"

export default function Home() {


  return <Page>

    <Title>
      Herzlich Willkommen!
    </Title>

    <br />


    <img className='mainPicture' src={picture1} alt="not found." style={{ borderRadius: 5 }}></img>
    <br />
    <br />

    Ich biete Ihnen
    <CustomLink route={RouteString.Angebote} pre=" " post=" ">emotionale Unterstützung</CustomLink>

    an
    und erzähle auf dieser Seite, was mich dazu bewegt.


    <br />
    <br />

    Jeder Mensch durchlebt im Laufe seines Lebens Phasen,
    die ihn fordern, verzweifeln lassen und langfristig prägen.
    <br />
    Ob Krise, Verlust, Umbruch oder Aufbruch, wir alle wünschen uns
    in diesen Zeiten zuverlässige und nahbare Gesprächspartner:innen.

    <br />
    <br />

    In meinem Leben gab es einige schmerzliche Jahre,
    die mir tiefe Einblicke in die menschliche Gefühlswelt,
    deren Verbindung zum Verstand
    und den dazugehörigen Verarbeitungsprozessen gewährt haben.



    <br />
    <br />

    Seit ich diese Zeit hinter mir habe,
    wird mir von
    Freund:innen,
    Bekannten und Klient:innen
    rückgemeldet
    ,
    wie sehr ihnen Gespräche mit mir helfen.
    Sie fühlen sich gehört und
    verstanden und können sich besser selbst beobachten und weiterentwickeln.
{/* 
    </CustomLink>
    <CustomLink route={RouteString.Motivation} pre=" " post="">
    <br />
    <br /> */}




    <br />
    <br />

    Heute freue ich mich darüber, dass ich meine Erfahrungen nutzen kann,
    um Menschen zu helfen und zu unterstützen.

    <br />
    <br />




    Machen Sie sich gern ein Bild von meinem Ansatz
    und zögern Sie nicht, mich bei Bedarf zu kontaktieren. 🙂

    <br />
    <br />
    <CustomLink route={RouteString.Motivation}>Mehr über meine Motivation</CustomLink>
    <br />
    <CustomLink route={RouteString.MeineGeschichte}>Oder interessiert sie meine Geschichte?</CustomLink>



  </Page>


}