import CustomLink from '../components/page/link';
import Page from '../components/page/page';
import Subtitle from '../components/page/subtitle';

import Title from '../components/page/title';
import { RouteString } from '../menuStructure';
import { useState } from 'react';
import React from 'react';


export default function Angebote() {

  return <Page>

    <Title>
      Angebot
    </Title>

    <br />
    <Subtitle>
      Wichtiger Hinweis
    </Subtitle>

    <br />
    Was ich anbiete, ist
    keine Therapie!
    <br />
    <br />
    Wenn Sie also ernsthafte psychische Probleme haben,
    wenden Sie sich bitte an Ihren Hausarzt und/oder Psychiater.

    <br />
    <br />

    {/* <CustomLink route={RouteString.Kontakt}>Nehmen Sie mein Angebot gerne wahr</CustomLink>, */}
    Nehmen Sie mein Angebot gerne wahr,
    wenn Ihnen der Weg zum Arzt als nicht notwendig erscheint
    und Sie natürlich keine akut krankhafte, oder gar suizidale Tendenzen haben.


    <br />
    <br />

    Wenn Sie bereits in Therapie sind und mein Angebot als zusätzliche Option wahrnehmen möchten,
    können wir gerne darüber sprechen, inwiefern eine Zusammenarbeit sinnvoll ist.
    
    <br />
    <br />

    <Subtitle>
      Wie sieht die erste Sitzung aus?
    </Subtitle>
    <br />


    In der ersten Sitzung werden wir uns offen über Ihr Anliegen unterhalten.

    Dabei werde ich ein paar Fragen stellen und anschließend überlegen wir gemeinsam,
    wie die Treffen erfolgreich sein können.

    <br />
    <br />

    Jeder Mensch entwickelt ganz eigene Strategien,
    Erfahrungen körperlich und geistig zu bewältigen.

    Mein Angebot ist demnach natürlich hochindividuell
    und beinhaltet Elemente, die genau auf Ihre Bedürfnisse
    zugeschnitten sind.


    <br />
    <br />
    <Subtitle>
      Welche Qualifikationen bringe ich mit?
    </Subtitle>
    <br />

    Meine Selbsterfahrungen sprechen für sich.

    Alle Gespräche und Ideen basieren auf meinen eigenen Erfahrungen
    
    sowie der tiefen menschlichen Überzeugung vom intensiven Zuhören
    und Raum geben.
    <br />
    <br />

    Plump gesagt:
    <br />

    Wenn Sie nach der ersten Sitzung
    nicht von meiner Qualifikation überzeugt sind,
    dann haben Sie nichts verloren,
    denn Sie zahlen nur,
    wenn Sie mit meiner Arbeit zufrieden sind.

    <br />
    <br />

    <Subtitle>
      Was biete ich genau an, was ist emotionale Unterstützung?
    </Subtitle>
    <br />

    Zunächst öffne ich für Sie meinen inneren Raum,
    in den Sie sich fallen lassen können.

    Sich gehört und verstanden zu fühlen
    und sich einfach mal auszusprechen kann schon viel 
    an dem Gefühlszustand ändern, probieren Sie es aus!

    <br />
    <br />

    Darüber hinaus biete ich bei Bedarf an,
    bestimmte Möglichkeiten vorzuschlagen,
    wie Sie ihr Thema mit mir gemeinsam,
    oder auch eigenständig
    weiter bearbeiten können.

    <br />
    Dabei geht es je nach Wunsch und Befürfnis
    um das direkte Erleben von Gefühlen,
    oder aber auch um abstrakte und herausfordernde Fragen über Weltbild und Lebenseinstellung.

    <br />
    <br />

    Fernab von diesen Möglichkeiten
    können wir uns natürlich auch einfach nur unterhalten
    und zur Ruhe kommen - oder ich gehe auf Ihre eigenen Vorstellungen und Ideen zu den Stunden ein.

    <br />
    <br />

    Ihr Wohlbefinden ist das einzige
    Anliegen unserer Sitzungen. 🙃

    <br />
    <br />

    <CustomLink route={RouteString.Kontakt} pre=" ">Wo finde ich die Kontaktdaten?</CustomLink>
    <br />

    <CustomLink route={RouteString.Preise}>Wie viel kostet eine Sitzung?</CustomLink>

  </Page>


}